:root {
	--nav-font-color: white;
	--nav-title-size: 2rem;
	--nav-title-weight: 600;
	--nav-font-size: 1.1rem;
	--nav-font-weight: 600;
	--nav-icon-size: 1.3rem;
	--nav-bg-color: #282c34;
	/* --body-bg-color: #F5F5F5; */
	--body-bg-color: #F5F5F0;
	/* --body-bg-color: #F3F3F3; */
	/* --body-bg-color: #e1e1e1; */
	--body-bg-color-accent: #708090;
	--project-card-font-color: white;

	/* --body-bg-color-accent: #e1e1e1; */
	/* --body-bg-color: #708090; */
	/* --project-card-font-color: black; */

	/* --body-bg-color: #282c34; */


	--body-font-color: black;
	--body-font-size: 1.2rem;
	--body-width: 720px;
}

* {
}

body {
	margin: 0;
	padding: 0;

	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
	'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	font-size: var(--body-font-size);
	background-color: var(--body-bg-color);
	padding-bottom: 100px;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
	monospace;
}

nav {
	background-color: #282c34;
	height: min-content;
	color: white;

}

.nav-container {
	width: max-content;
	padding: 2rem;
	margin: 0 auto;

}

.nav--top-section {
	font-size: var(--nav-title-size);
	font-weight: var(--nav-title-weight);

}

.webname, .nav--button {
	color: white;
	text-decoration: none;
}

.nav--bottom-section {
	font-size: var(--nav-font-size);
	font-weight: var(--nav-font-weight);
	padding: 10px 0 0 0;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
}

.nav--icons {
	display: flex;
	align-items: center;
}

.nav--button-icon {
	margin: 0 0 0 15px;
	width: var(--nav-icon-size);
}

.content-container {
	margin: auto;
	padding: 20px;
	max-width: var(--body-width);
	line-height: 1.6;
	text-align: justify;
}

p {
	padding: 0;
	margin: 0;
}

hr {
	margin: 3rem 0 3rem 0;
	padding: 0;
	background-color: lightgray;
	border: none;
	height: 2px;
	width: 100%;
}

/* Projects */

.Projects {
	padding-left: 2%;
	padding-right: 2%;
	margin: auto;
	margin-top: 1rem;
	max-width: var(--body-width);
}

.cards-list {
	/* margin: auto; */
    display: flex;
	flex-direction: column;
    /* flex-wrap: nowrap; */
    /* gap: 20px; */
    /* overflow-x: auto; */
	/* justify-content: flex-start; */
}

.card {
	background-color: var(--body-bg-color-accent);
	margin: 20px;
    /* max-width: var(--body-width); */
	/* height: 400px; */
	/* height: 200px; */
    font-size: 1.3rem;
    display: flex;
    flex-direction: row;
	color: var(--project-card-font-color);
	border-radius: 25px;
}

.card--image-section {
	/* height: 50%; */
	width: 50%;
	padding: 3%;
	border-radius: 25px 0 0 25px;
}

.card--image {
	height: 100%;
	width: 100%;
}

.card--content-section {
	display: flex;
	flex-direction: column;
	width: 50%;
}

.card--top-section {
	padding: 10px;
}

.card--title {
	font-size: 1.8rem;
	font-weight: 500;
}

.card--discription {
	font-size: 1rem;
}

.card--bottom-section {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.card--tech {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 10px;
	padding: 10px;
}

.card--tech-item {
	background-color: black;
	padding: 10px;
	border-radius: 8px;
	font-weight: 600;
}

.card--tech-item-image {
	/* background-color: white; */
	padding: 2px;
	/* border-radius: 15px; */
	width: 3rem;
}

.card--links {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	padding: 10px;
	gap: 20px;
}

.card--icon {
	background-color: white;
	padding: 8px;
	border-radius: 10px;
	width: 3rem;
}

/* ALGORITHMS START */

.Algorithms {
	padding-left: 2%;
	padding-right: 2%;
	margin: auto;
	margin-top: 2rem;
	max-width: var(--body-width);
	display: flex;
	flex-direction: column;
}

.title-algorithms {
	padding: 0;
	margin: 0;
	font-size: 2rem;
}

#sortingCanvas {
	border: 2px solid lightgray;
	border-radius: 10px;
	margin-bottom: 1rem;
	padding: 0 8px 0 8px;
}

/* ALGO CONTROLS */

.controls {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.section--sort {
	margin: 0px 0px 10px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 5px;
	justify-content: space-between;
}

.section--generate {
	/* max-width: var(--body-width); */
	display: flex;
	align-items: center;
	background-color: lightgray;
	padding: 10px
}

.section--discription {
	max-width: var(--body-width);
}

button {
	background-color: darkgray;
	margin: 0px;
	border: 0;
	border-radius: 5px;
	padding: 8px 10px;
	font-size: 1rem;
	color: white;
}

button:hover {
	opacity: 0.7;
}

button:active {
	background-color: indianred;
}

.button-selected {
	background: indianred;
}

.button-selected:hover {
	background: indianred;
}

.button--refresh {
	padding: 5px 10px;
	border-radius: 5px;
	width: 2rem;
	height: 2rem;
	background-image: url('./images/refresh-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 65%;
}

.slider-container {
  width: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.slider {
  -webkit-appearance: none;
  width: 90%;
  height: 10px;
  border-radius: 5px;
  background: white;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #008B8B;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #04AA6D;
  cursor: pointer;
}

.slider-icons {
	margin: 0;
	width: 24px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-end;
}

/* Archive */

.Archive {
	max-width: calc(var(--body-width) - 200px);
	margin: auto;

}

.archive-container {
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding: 20px;
}

.archive--link {
	text-decoration: none;
	font-size: 1.5rem;
	color: black;
	font-weight: 450;
}

.archive--li {
	padding-left: 3rem;
}

@media only screen and (max-width: 720px) {
	.cards-list {
		flex-direction: row;
		flex-wrap: nowrap;
		overflow-x: auto;
		justify-content: flex-start;
		gap: 20px;
		margin: 0;
	}
	.card {
		flex-direction: column;
		font-size: 1rem;
		min-width: 80%;
		margin: 0;
		height: 100%;
	}

	.card--title {
		font-size: 1.6rem;
	}

	.card--image-section {
		min-width: 80%;
		margin: auto;

		padding: 8%;
		padding-bottom: 0;
		border-radius: 25px 0 0 25px;
	}

	.card--image {
		width: 100%;

	}

	.card--content-section {
		height: 50%;
		width: 100%;
	}

}
